<template>
  <v-dialog v-model="isOpen" persistent width="350px">
    <v-card>
      <v-card-title>
        <span class="headline">Mass Edit</span>
      </v-card-title>
      <v-card-text class="text-start">
        <v-select v-if="editField === 'point_object_type'" v-model="editAll[editField]" :items="point_object_types" :label="toUpperTextKey(editField)" ></v-select>
        <v-checkbox v-if="typeof editAll[editField] === 'boolean'" v-model="editAll[editField]" :label="toUpperTextKey(editField)"/>
        <v-text-field v-if="typeof editAll[editField] === 'number'" v-model.number="editAll[editField]" :label="toUpperTextKey(editField)" type="number" @input="numberChange"></v-text-field>
        <v-text-field v-if="typeof editAll[editField] === 'string' && editField != 'point_object_type'" v-model="editAll[editField]" :label="toUpperTextKey(editField)" ></v-text-field>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn text @click="onClose">Close</v-btn>
        <v-btn color="primary" text @click="onSave(editAll[editField], editField)">Save</v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import { toUpperTextKey } from '../../utils/utils';
import { POINT_OBJECT_TYPES } from './bacnetMasterConstant';

export default {
  props: ['on-close', 'is-open', 'on-save', 'edit-all', 'edit-field'],
  data: () => ({
    toUpperTextKey: toUpperTextKey, 
    point_object_types: [...POINT_OBJECT_TYPES],
  }),
  methods:{
    numberChange(val){
      this.editAll[this.editField] = val ? Number(val) : 0;
    },
  }
};
</script>


