<template>
  <v-app>
    <right-sidebar :menuItems="networksData" serverRoute="bacnet-master"></right-sidebar>
    <v-main>
      <div class="table-styling">
        <v-tabs v-model="tab" right :color="`${$vuetify.theme.dark ? '' : 'nubeGreen'}`">
          <breadcrumbs-direction :items="dirs" />
          <v-tab :key="0" style="margin-left: auto"> Data </v-tab>
          <v-tab :key="1"> System Status </v-tab>
        </v-tabs>
        <v-tabs-items v-model="tab">
          <v-tab-item :key="0">
            <v-expansion-panels v-model="panel" multiple>
              <v-expansion-panel v-for="(network, i) in networks" :key="i">
                <v-expansion-panel-header hide-actions>
                  <template v-slot:default="{ open }">
                    <b>NETWORK: {{ network.network_name }}</b>
                    <v-icon style="text-align: end; display: block">
                      {{ open ? 'mdi-chevron-down' : 'mdi-chevron-right' }}
                    </v-icon>
                  </template>
                </v-expansion-panel-header>
                <v-expansion-panel-content>
                  <v-data-table
                    v-if="dataTable[network.network_name].length > 0"
                    v-model="selected"
                    :headers="pointHeaders"
                    :items="dataTable[network.network_name]"
                    class="elevation-1 text-left"
                    item-key="point_uuid"
                    show-select
                    :footer-props="tableProps.FOOTER_PROPS"
                    :itemsPerPage="tableProps.ITEMS_PER_PAGE"
                    :search="search"
                    sort-by="point_uuid"
                    group-by="category"
                    show-group-by
                    :custom-filter="filterCriteria"
                    dense
                  >
                    <template v-slot:top>
                      <v-toolbar flat>
                        <v-toolbar-title>BACnet Points View</v-toolbar-title>
                        <v-divider class="mx-4" inset vertical></v-divider>
                        <div class="text-left pa-2">
                          <v-btn x-small class="mr-1 btn-height-22 white--text" color="primary" @click="onClickRefresh">
                            Refresh<v-icon x-small right>mdi-refresh</v-icon></v-btn
                          >
                          <v-btn
                            x-small
                            class="mr-1 btn-height-22 white--text"
                            color="error"
                            @click="clickDeleteMulti"
                            :disabled="selected.length === 0"
                            >Delete<v-icon x-small right>mdi-delete</v-icon></v-btn
                          >
                        </div>
                        <v-spacer></v-spacer>
                        <v-text-field
                          v-model="search"
                          append-icon="mdi-magnify"
                          label="Search"
                          single-line
                          @input="onSearchChange"
                          hide-details
                        ></v-text-field>
                      </v-toolbar>
                    </template>

                    <template v-slot:[`group.header`]="{ group, headers, toggle, isOpen, items }">
                      <td :colspan="headers.length">
                        <div class="row" style="align-items: center">
                          <v-btn @click="toggle" x-small icon :ref="group">
                            <v-icon>{{
                              isOpen ? 'mdi-chevron-double-down' : 'mdi-chevron-double-right'
                            }}</v-icon> </v-btn
                          >&nbsp;&nbsp;&nbsp;&nbsp;
                          <v-checkbox
                            v-bind:value="
                              selected.filter(point => point.device_uuid === items[0].device_uuid).length ==
                              items.length
                            "
                            @change="check($event, items)"
                          ></v-checkbox>
                          <router-link
                            :to="`/bacnet-master/devices/${items[0].device_uuid}/points`"
                            class="font-weight-bold text-style"
                          >
                            {{ group }}
                          </router-link>
                        </div>
                      </td>
                    </template>

                    <template v-slot:[`item.point_enable`]="{ item }">
                      <v-checkbox v-model="item.point_enable" @click="updatePoint(item)"></v-checkbox>
                    </template>
                    <template v-slot:[`item.feedback`]="{ item }">
                      <v-checkbox v-model="item.feedback" @click="updatePoint(item)"></v-checkbox>
                    </template>
                    <template v-slot:[`item.get_priority`]="{ item }">
                      <v-checkbox v-model="item.get_priority" @click="updatePoint(item)"></v-checkbox>
                    </template>
                    <template v-slot:[`item.point_writable`]="{ item }">
                      <v-checkbox v-model="item.point_writable" @click="updatePoint(item)"></v-checkbox>
                    </template>
                    <template v-slot:[`item.point_object_type`]="{ item }">
                      <v-edit-dialog
                        :return-value.sync="item.point_object_type"
                        @save="updatePoint(item)"
                        @cancel="cancelEdit"
                        @open="openEdit(item)"
                        @close="closeEdit"
                        large
                      >
                        <b>{{ item.point_object_type }}</b>
                        <template v-slot:input>
                          <v-select v-model="item.point_object_type" :items="point_object_types"></v-select>
                        </template>
                      </v-edit-dialog>
                    </template>
                    <template v-slot:[`item.point_object_id`]="{ item }">
                      <v-edit-dialog
                        :return-value.sync="item.point_object_id"
                        @save="updatePoint(item)"
                        @cancel="cancelEdit"
                        @open="openEdit(item)"
                        @close="closeEdit"
                        large
                      >
                        <b>{{ item.point_object_id }}</b>
                        <template v-slot:input>
                          <v-text-field
                            v-model="item.point_object_id"
                            single-line
                            type="number"
                            @input="
                              val => {
                                val ? (item.point_object_id = val) : (item.point_object_id = 0);
                              }
                            "
                          ></v-text-field>
                        </template>
                      </v-edit-dialog>
                    </template>
                    <template v-slot:[`item.point_value`]="{ item }">
                      <v-edit-dialog
                        :return-value.sync="item.point_value"
                        @save="updatePoint(item)"
                        @cancel="cancelEdit"
                        @open="openEdit(item)"
                        @close="closeEdit"
                        large
                      >
                        <b>{{ item.point_value }}</b>
                        <template v-slot:input>
                          <v-text-field
                            v-model="item.point_value"
                            single-line
                            type="number"
                            min="0"
                            @input="
                              val => {
                                val ? (item.point_value = val) : (item.point_value = 0);
                              }
                            "
                          ></v-text-field>
                        </template>
                      </v-edit-dialog>
                    </template>
                    <template v-slot:[`item.priority`]="{ item }">
                      <v-edit-dialog
                        :return-value.sync="item.priority"
                        @save="updatePoint(item)"
                        @cancel="cancelEdit"
                        @open="openEdit(item)"
                        @close="closeEdit"
                        large
                      >
                        <b>{{ item.priority }}</b>
                        <template v-slot:input>
                          <v-text-field
                            v-model="item.priority"
                            single-line
                            type="number"
                            @input="
                              val => {
                                val ? (item.priority = val) : (item.priority = 0);
                              }
                            "
                          ></v-text-field>
                        </template>
                      </v-edit-dialog>
                    </template>
                    <template v-slot:[`item.timeout`]="{ item }">
                      <v-edit-dialog
                        :return-value.sync="item.timeout"
                        @save="updatePoint(item)"
                        @cancel="cancelEdit"
                        @open="openEdit(item)"
                        @close="closeEdit"
                        large
                      >
                        <b>{{ item.timeout }}</b>
                        <template v-slot:input>
                          <v-text-field
                            v-model="item.timeout"
                            single-line
                            type="number"
                            @input="
                              val => {
                                val ? (item.timeout = val) : (item.timeout = 0);
                              }
                            "
                          ></v-text-field>
                        </template>
                      </v-edit-dialog>
                    </template>
                    <template v-slot:[`item.priority_array`]="{ item }">
                      <div class="overflow-td">
                        <span
                          v-for="(key, index) of item.priority_array ? Object.keys(item.priority_array) : []"
                          :key="index"
                        >
                          {{ key }}:&nbsp;<span class="value">{{
                            item.priority_array[key] || item.priority_array[key] == 0
                              ? item.priority_array[key]
                              : 'null'
                          }}</span
                          >,&nbsp;
                        </span>
                      </div>
                    </template>

                    <template v-slot:[`item.actions`]="{ item }">
                      <v-icon small class="mr-2" @click="editItem(item)" color="primary"> mdi-pencil </v-icon>
                      <v-icon small @click="clickDelete(item)" color="error"> mdi-delete </v-icon>
                    </template>

                    <!-- header -->
                    <template v-slot:[`header.point_enable`]>
                      {{ toUpperTextKey('enable') }}&nbsp;
                      <v-icon
                        x-small
                        @click="openMassEditDialog('point_enable'), $event.stopPropagation()"
                        color="primary"
                        >mdi-pencil</v-icon
                      >&nbsp;
                    </template>
                    <template v-slot:[`header.point_object_type`]>
                      {{ toUpperTextKey('object_type') }}&nbsp;
                      <v-icon
                        x-small
                        @click="openMassEditDialog('point_object_type'), $event.stopPropagation()"
                        color="primary"
                        >mdi-pencil</v-icon
                      >&nbsp;
                    </template>
                    <template v-slot:[`header.feedback`]>
                      {{ toUpperTextKey('feedback') }}&nbsp;
                      <v-icon x-small @click="openMassEditDialog('feedback'), $event.stopPropagation()" color="primary"
                        >mdi-pencil</v-icon
                      >&nbsp;
                    </template>
                    <template v-slot:[`header.get_priority`]>
                      {{ toUpperTextKey('get_priority') }}&nbsp;
                      <v-icon
                        x-small
                        @click="openMassEditDialog('get_priority'), $event.stopPropagation()"
                        color="primary"
                        >mdi-pencil</v-icon
                      >&nbsp;
                    </template>
                    <template v-slot:[`header.point_value`]>
                      {{ toUpperTextKey('value') }}&nbsp;
                      <v-icon
                        x-small
                        @click="openMassEditDialog('point_value'), $event.stopPropagation()"
                        color="primary"
                        >mdi-pencil</v-icon
                      >&nbsp;
                    </template>
                    <template v-slot:[`header.point_writable`]>
                      {{ toUpperTextKey('writable') }}&nbsp;
                      <v-icon
                        x-small
                        @click="openMassEditDialog('point_writable'), $event.stopPropagation()"
                        color="primary"
                        >mdi-pencil</v-icon
                      >&nbsp;
                    </template>
                    <template v-slot:[`header.priority`]>
                      {{ toUpperTextKey('priority') }}&nbsp;
                      <v-icon x-small @click="openMassEditDialog('priority'), $event.stopPropagation()" color="primary"
                        >mdi-pencil</v-icon
                      >&nbsp;
                    </template>
                    <template v-slot:[`header.timeout`]>
                      {{ toUpperTextKey('timeout') }}&nbsp;
                      <v-icon x-small @click="openMassEditDialog('timeout'), $event.stopPropagation()" color="primary"
                        >mdi-pencil</v-icon
                      >&nbsp;
                    </template>
                  </v-data-table>
                  <div v-else>No data</div>
                </v-expansion-panel-content>
              </v-expansion-panel>
            </v-expansion-panels>
            <v-overlay :value="loadingData">
              <v-progress-circular indeterminate size="64" color="primary"></v-progress-circular>
            </v-overlay>
          </v-tab-item>
          <v-tab-item :key="1">
            <system-status-table server="bacnet-master"></system-status-table>
          </v-tab-item>
        </v-tabs-items>
      </div>
    </v-main>

    <!--Edit dialog-->
    <bm-point-form-dialog
      :is-open="dialog"
      :on-close="close"
      :on-save="() => updatePoint(editedItem)"
      :form-title="'Edit Device'"
      :device-name="editedItem && editedItem.category ? editedItem.category.split(':')[1] : ''"
      :edited-item="editedItem"
      :on-check-existing="isExistingvalue"
    ></bm-point-form-dialog>

    <!-- Delete dialog -->
    <confirm-dialog
      :is-open="dialogDelete"
      :on-close="closeDelete"
      :on-confirm="confirm"
      dialog-text="Do you want to delete ?"
    ></confirm-dialog>

    <!-- Mass Edit dialog-->
    <bm-mass-edit-dialog
      :is-open="dialogMassEdit"
      :on-close="() => (dialogMassEdit = false)"
      :edit-all="editAll"
      :edit-field="editField"
      :on-save="saveMassEdit"
    ></bm-mass-edit-dialog>
  </v-app>
</template>

<script>
import axios from '../../axios';
import _cloneDeep from 'lodash/cloneDeep';
import MqttConsole from '../Mqtt/MqttConsole';
import RightSidebar from '../Common/RightSidebar';
import ConfirmDialog from '../Common/ConfirmDialog';
import SystemStatusTable from '../Common/SystemStatusTable.vue';
import BreadcrumbsDirection from '../Common/BreadcrumbsDirection.vue';
import BmMassEditDialog from './BmMassEditDialog';
import BmPointFormDialog from './BmPointFormDialog';
import { DATA_TABLE_PROPS } from '../../constants/appConstant';
import { POINT_HEADERS, POINT_OBJECT_TYPES, RULES, POINT_EDIT_ALL_DEFAULT } from './bacnetMasterConstant';
import { toUpperTextKey } from '../../utils/utils';
import { updatePostPointData } from './bacnetMasterUtils';

export default {
  components: {
    MqttConsole,
    RightSidebar,
    ConfirmDialog,
    BmMassEditDialog,
    BmPointFormDialog,
    SystemStatusTable,
    BreadcrumbsDirection,
  },
  data: () => ({
    toUpperTextKey: toUpperTextKey,
    action: '',
    network: {},
    valid: false,
    dialog: false,
    dialogDelete: false,
    selected: [],
    pointHeaders: [...POINT_HEADERS],
    point_object_types: [...POINT_OBJECT_TYPES],
    currentItem: {},
    editedItem: {},
    rules: { ...RULES },
    search: '',
    searchItems:[],
    dialogMassEdit: false,
    editAll: {},
    editField: '',
    intervalId: null,
    expanded: [],
    pointsOfDevice: [],
    dataTable: {},
    dirs: [
      { text: 'NETWORKS', link: '/bacnet-master/networks', disabled: false },
      { text: 'POINTS VIEW', disabled: true },
    ],
    networks: [],
    panel: [0],
    tableProps: { ...DATA_TABLE_PROPS },
    tab: 0,
    loadingData: false,
  }),
  computed: {
    debugSubscriptions() {
      const { debugSubscriptions } = this.$store.state.mqttConfig;
      return debugSubscriptions.bacnet;
    },
    setting() {
      return this.$store.state.setting;
    },
    networksData() {
      return this.$store.state.bmNetworks;
    },
  },
  watch: {
    'dialog'(val) {
      val || this.close();
    },
    'dialogDelete'(val) {
      val || this.closeDelete();
    },
    '$route.params.id': function (val, oldVal) {
      if (val != oldVal) {
        this.getAllPoints();
      }
    },
  },
  beforeRouteLeave(to, from, next) {
    clearInterval(this.intervalId);
    next();
  },
  mounted() {
    this.getAllPoints();

    //set auto refresh
    if (this.setting.refreshTime && this.setting.enableAutoRefresh) {
      this.intervalId = setInterval(() => {
        this.getAllPoints();
        if (this.setting.toastAutoRefresh) {
          this.$toasted.show(`Automatically refresh every ${this.setting.refreshTime}ms`, { type: 'info' });
        }
      }, this.setting.refreshTime);
    }
  },
  methods: {
    getAllPoints() {
      this.loadingData = true;
      axios.get(`bacnet_master/networks`).then(data => {
        this.networks = data.networks;
        this.getDataTable(this.networks);
        this.loadingData = false;
      });
    },
    getDataTable(networks) {
      this.dataTable = {};
      networks.forEach(network => {
        this.dataTable[network.network_name] = [];
        network.devices.forEach(device => {
          if (device.points.length > 0) {
            const pointsPerDevice = device.points.map(point => {
              point = { ...point, category: `DEVICE: ${device.device_name}`, network_name: network.network_name };
              return point;
            });
            this.dataTable[network.network_name] = this.dataTable[network.network_name].concat(pointsPerDevice);
          }
        });
      });
    },
    editItem(item) {
      this.editedIndex = this.dataTable[item.network_name].findIndex(p => p.point_uuid == item.point_uuid);
      this.pointsOfDevice = this.dataTable[item.network_name].filter(point => point.device_uuid === item.device_uuid);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },
    async clickDelete(item) {
      this.action = 'deleteSingle';
      this.dialogDelete = true;
      this.currentItem = item;

      this.editedItem = Object.assign({}, item);
    },
    async clickDeleteMulti() {
      this.dialogDelete = true;
      this.action = 'deleteSelected';
    },
    async deleteSingle() {
      this.editedIndex = this.dataTable[this.currentItem.network_name].indexOf(this.currentItem);
      this.dataTable[this.currentItem.network_name].splice(this.editedIndex, 1);
      this.dataTable[this.currentItem.network_name] = [...this.dataTable[this.currentItem.network_name]];
      await axios.delete(`bacnet_master/point/${this.currentItem.point_uuid}`);
      this.closeDelete();
    },
    async deleteSelected() {
      const promises = [];
      for (const item of this.selected) {
        this.currentItem = item;
        promises.push(this.deleteSingle());
      }
      await Promise.all(promises);
      this.selected = [];
    },
    async confirm() {
      await this[this.action]();
      this.dialog = false;
      this.action = '';
    },
    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = {};
        this.editedIndex = -1;
      });
    },
    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = {};
        this.editedIndex = -1;
      });
    },
    isExistingvalue(item, fieldName) {
      if (!this.pointsOfDevice) return false;
      const value = item[fieldName];
      let points = _cloneDeep(this.pointsOfDevice);
      const index = points.findIndex(p => p.point_uuid == item.point_uuid);
      points.splice(index, 1);
      return points.find(p => p[fieldName] == value) != null;
    },
    updatePoint(data) {
      const _data = updatePostPointData(data);
      axios.patch(`bacnet_master/point/${data.point_uuid}`, _data).then(response => {
        const index = this.dataTable[data.network_name].findIndex(n => n.point_uuid == response.point_uuid);
        Object.assign(this.dataTable[data.network_name][index], response);
        this.$toasted.show('update success');
        this.close();
        this.expanded = [];
      });
    },
    saveMassEdit(value, field) {
      for (const item of this.selected) {
        item[field] = value;
        this.updatePoint(item);
      }
      this.dialogMassEdit = false;
    },
    async openMassEditDialog(fieldName) {
      if (this.selected.length < 1) {
        return this.$toasted.show('Please select item first!', { type: 'error' });
      }
      this.dialogMassEdit = true;
      this.editField = fieldName;
      this.editAll = { ...POINT_EDIT_ALL_DEFAULT };
    },
    cancelEdit() {},
    openEdit(item) {
      this.editedIndex = this.dataTable[item.network_name].indexOf(item);
      this.editedItem = Object.assign({}, item);
    },
    closeEdit() {
      this.editedIndex = -1;
      this.editedItem = {};
    },
    expandItem({ item }) {
      this.editedIndex = this.dataTable[item.network_name].indexOf(item);
      this.editedItem = Object.assign({}, item);
    },
    onClickRefresh() {
      this.getAllPoints();
      this.$toasted.show(`successfully refresh`, { type: 'success' });
    },
    check(e, items) {
      if (!e) this.selected = [...this.selected].filter(point => point.device_uuid != items[0].device_uuid);
      else this.selected = [...this.selected].concat(items);
    },
    onSearchChange() {
      this.searchItems = this.search.split(',').map(val => val.trim());
    },
    filterCriteria(value, search, item) {
      return (
        value != null &&
        this.searchItems &&
        Array.isArray(this.searchItems) &&
        this.searchItems.length > 0 &&
        this.searchItems.every(val => typeof val === 'string') &&
        this.searchItems.some(searchVal => value.toString().toLowerCase().indexOf(searchVal.toLowerCase()) !== -1)
      );
    },
  },
};
</script>

