export function updatePostNetworkData(network){
  return {
    network_device_name: network.network_device_name,
    network_device_object_id: Number(network.network_device_object_id) || 0,
    network_enable: Number(network.network_enable) || 0,
    network_ip: network.network_ip,
    network_mask: Number(network.network_mask) || 0,
    network_name: network.network_name,
    network_port: Number(network.network_port) || 0,
  }
};

export function updatePostDeviceData(device){
  return {
    device_enable: device.device_enable || 0,
    device_ip: device.device_ip,
    device_mac: Number(device.device_mac) || 0,
    device_mask: Number(device.device_mask) || 0,
    device_name: device.device_name,
    device_object_id: Number(device.device_object_id) || 0,
    device_port: Number(device.device_port) || 0,
    network_number: Number(device.network_number) || 0,
    network_uuid: device.network_uuid,
    supports_rpm: device.supports_rpm || false,
    supports_wpm: device.supports_wpm || false,
    type_mstp: device.type_mstp || false,
    ethernet_mac_address: device.ethernet_mac_address,
    manufacture: device.manufacture,
  }
};

export function updatePostPointData(point){
  return {
    device_uuid: point.device_uuid,
    point_enable: point.point_enable || false,
    point_name: point.point_name,
    point_object_id: Number(point.point_object_id) || 0,
    point_object_type: point.point_object_type,
    point_store: point.point_store,
    feedback: point.feedback,
    point_writable: point.point_writable,
    get_priority: point.get_priority,
    // point_value: Number(point.point_value) || 0,
    // priority: Number(point.priority) || 0,
    timeout: Number(point.timeout) || 0,
  }
};